import Vue from 'vue'
import VueRouter from 'vue-router'

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/HomeView.vue'),
    redirect: '/home', //重定向到首页,
    children: [
      { path: "/home", name: "Home", meta: { name: '系统首页' }, component: () => import('../views/manager/Home.vue') },
      { path: "/elf", name: "Elf", meta: { name: '全国图鉴' }, component: () => import('../views/manager/Elf.vue'),},
      { path: '/wiki', name: 'Wiki', meta: { name: '详细信息'}, component: () => import("../views/manager/Information.vue")},
      { path: "/moves", name: "Moves", meta: { name: '招式列表' }, component: () => import('../views/manager/Moves.vue') },
      { path: "/characteristic", name: "Characteristic", meta: { name: '特性列表' }, component: () => import('../views/manager/Characteristic.vue') },
      { path: "/elfCharacteristic", name: "ElfCharacteristic", meta: { name: '精灵特性' }, component: () => import('../views/manager/ElfCharacteristic.vue') },
      { path: "/prop", name: "Prop", meta: { name: '未完成' }, component: () => import('../views/manager/prop.vue') },
      { path: "/place", name: "Place", meta: { name: '未完成' }, component: () => import('../views/manager/place.vue') },
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
